import React from 'react';
import s from './Post.module.css';

const Post = (props) => {
    return (
        <div className={`${s.content} ${s.item}`}>
            <img src='https://rg.ru/uploads/images/2022/11/02/ovot_cf9.jpg' />
            {props.message};
            <div>
                <span>like</span> {props.LikesCount}
            </div>
        </div>
    )
}
export default Post;