import React from 'react';
import s from './myPosts.module.css';
import Post from './Post/Post';
import {useForm} from "react-hook-form";


const MyPosts = React.memo(props => {
    console.log("render yo");
    let postsElements = props.posts.map(p => <Post message={p.message} LikesCount={p.LikesCount}/>)

    const {register, handleSubmit, formState: {errors, isValid}, reset} = useForm({mode: "onBlur"});

    const onSubmit = (data) => {
        props.addPost(data.newPostText);
        alert(JSON.stringify(data));
        reset();
    }
    return (
        <div>
            <div className={s.postsBlock}>
                <h3> My posts </h3>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <div>
                        <textarea {...register("newPostText", {
                            required: 'A must-have field',
                            minLength: {
                                value: 5,
                                message: 'minimum 5 symbols!'
                            }
                        })}
                                  placeholder={'add new text'}/>

                    </div>
                    <div style={{color: "red"}}>
                        {errors?.newPostText && <div>{errors?.newPostText?.message || 'Error!'}</div>}
                    </div>
                    <div>
                        <button disabled={!isValid}>Post</button>
                    </div>
                </form>
            </div>
            <div className={s.postsBlock}>
                {postsElements}
            </div>
        </div>
    )
});

