import React from "react";

const SEND_MESSAGE = 'SEND_MESSAGE';

let initialState = {
    "dialogs": [
        {"id": 1, "name": 'Nastya', "img": <img src="https://otkritkis.com/wp-content/uploads/2021/10/ava-95.jpg"/>},
        {"id": 2, "name": 'Dima', "img": <img src="https://otkritkis.com/wp-content/uploads/2021/10/ava-95.jpg"/>},
        {"id": 3, "name": 'Oksana', "img": <img src="https://otkritkis.com/wp-content/uploads/2021/10/ava-95.jpg"/>},
        {"id": 4, "name": 'Lena', "img": <img src="https://otkritkis.com/wp-content/uploads/2021/10/ava-95.jpg"/>},
        {"id": 5, "name": 'Mama', "img": <img src="https://otkritkis.com/wp-content/uploads/2021/10/ava-95.jpg"/>}
    ],
    "messages": [
        {"id": 1, "message": "Hey! How is it going?"},
        {"id": 2, "message": "Ok! Getting better. You?"},
        {"id": 3, "message": "I'm still ill:("},
    ]
}

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case SEND_MESSAGE:
            let body = action.newMessageBody;
            return {
                ...state,
                messages: [...state.messages, {"id": 4, "message": body}]
            };

        default: return state;
    }

}

export const sendMessageCreator = (newMessageBody) => ({type:SEND_MESSAGE, newMessageBody})

export default dialogsReducer;