import React from 'react';
import s from './Dialogs.module.css';
import {Navigate, NavLink} from "react-router-dom";
import {useForm} from "react-hook-form";


const DialogItem = (props) => {
    return <div className={`${s.dialog} ${s.active} ${s.dialogPhoto}`}>
        {/*<img src='https://rg.ru/uploads/images/2022/11/02/ovot_cf9.jpg' />*/}
        <NavLink to={"/dialogs/" + props.id} className={navData => navData.isActive ? s.active: s.item}> {props.img} {props.name} </NavLink>
    </div>
}

const Message = (props) => {
    return <div> {props.message} </div>
}

const Dialogs = (props) => {

    let state = props.dialogsPage;
    let dialogsElements = state.dialogs.map(d => <DialogItem img={d.img} name={d.name} key={d.id} id={d.id}/>)
    let messageElements = state.messages.map(m => <Message message={m.message} key={m.id}/>)
    let newMessage = state.newMessage;

    const {register, handleSubmit, formState: {errors}, reset} = useForm();

    const onSubmit = (data) => {
        // alert(JSON.stringify(data.newMessageBody))
        props.sendMessage(data.newMessageBody);
        reset();
    }

    return (
        <div className={s.dialogItems}>
            <div className={s.dialogs}>
                {dialogsElements}
            </div>
            <div className={s.messages}>
                    <div>{messageElements}</div>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <div>
                        <textarea {...register("newMessageBody", {
                            maxLength: {
                                value: 100,
                                message: 'maximum 100 symbols!'
                            }})}
                                  placeholder='Enter your message'>
                        </textarea>
                    </div>
                    <div style={{color: "red"}}>
                        {errors?.newMessageBody && <div>{errors?.newMessageBody?.message || 'Error!'}</div>}
                    </div>
                    <div>
                        <button> Send </button>
                    </div>
                </form>
            </div>
        </div>
    )
}

