import React from 'react';
import {connect} from 'react-redux';
import {
    follow,
    setCurrentPage,
    unfollow, toggleFollowingProgress, getUsers
} from '../../redux/users-reducer';
import Users from './Users';
import Preloader from "../common/Preloader/Preloader";
import {compose} from "redux";
import {
    getPageSize,
    getUsersPages,
    getTotalUsersCount,
    getCurrentPage,
    getIsFetching, getFollowingInProgress
} from "../../redux/users-selectors";


class UsersContainer extends React.Component {

    componentDidMount() {
        let {currentPage, pagesSize} = this.props;
        this.props.getUsers(currentPage, pagesSize);
    }

    onPageChanged = (pageNumber) => {
        let {setCurrentPage, getUsers, pagesSize} = this.props;
        setCurrentPage(pageNumber);
        getUsers(pageNumber, pagesSize);
    }

    render() {
        return <>
            {this.props.isFetching ? <Preloader /> : null}
            <Users totalUsersCount={this.props.totalUsersCount}
                   pagesSize={this.props.pagesSize}
                   currentPage={this.props.currentPage}
                   users={this.props.users}
                   onPageChanged={this.onPageChanged}
                   follow={this.props.follow}
                   unfollow={this.props.unfollow}
                   followingInProgress={this.props.followingInProgress}
            />
        </>
    }

}

let mapStateToProps = (state) => {
    return {
        users: getUsersPages(state),
        pagesSize: getPageSize(state),
        totalUsersCount:  getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state)
    }
}

export default compose(
    connect(mapStateToProps,
            { follow, unfollow, setCurrentPage,
                toggleFollowingProgress, getUsers
            }),
)(UsersContainer)
