import React from "react";
import {useForm} from "react-hook-form";
import {connect} from "react-redux";
import {login} from "../../redux/auth-reducer";
import {useNavigate} from "react-router";
import style from "./Login.module.css";

const Login = ({login,isAuth, errorResponse}) => {
    const {register, handleSubmit, formState: {errors, isValid} } = useForm({mode: "onBlur"});

    const onSubmit = (d) => {
        login(d.email, d.password, d.rememberMe);
    }

    const navigate = useNavigate();
    if (isAuth) {
        return navigate ("/profile")
    }

    return (
        <form onSubmit={handleSubmit(onSubmit)}>
            <div>
                <h1>Login</h1>
                <input {...register("email", {
                    required: 'A must-have field',
                    minLength: {
                        value: 5,
                        message: 'minimum 5 symbols!'
                    }
                })}
                       placeholder={'email'}/>
            </div>
            <div style={{color: "red"}}>
                {errors?.email && <div>{errors?.email?.message || 'Error!'}</div>}
            </div>
            <div>
                <input type={"password"} {...register("password", {
                    required: 'A must-have field',
                    minLength: {
                        value: 8,
                        message: 'minimum 8 symbols!'
                    }
                })}
                    placeholder={'password'}/>
            </div>
            <div style={{color: "red"}}>
                {errors?.password && <div>{errors?.password?.message || 'Error!'} </div>
                }
            </div>
            <div>
                <input {...register("rememberMe")} type={'checkbox'} /> remember me
            </div>
            {errorResponse.length >0 && <div className={style.errorBlock}> {errorResponse}</div>}
            <div>
                <button disabled={!isValid}> Login </button>
            </div>
        </form>
    )
}

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth,
    errorResponse: state.auth.error
})
export default connect(mapStateToProps, {login})
(Login)
